.bullet-list {
  list-style: none;
  padding: 0rem 0rem 0rem 0.0rem;

  @media (max-width: 350px) {
    padding-left: 0.6875rem;
  }

  &__item {
    position: relative;
    margin-bottom: 0.625rem;
    padding-left: 1.3125rem;
    line-height: 1.5rem;
  }

  &__item:before {
    position: absolute;
    top: 0.4375rem;
    left: 0rem;
    content: " ";
    width: 0.625rem;
    height: 0.625rem;
    border-radius: 50%;
    background-color: $dark-shade-25;
  }
}